import logo from '../assets/logo.jpg';
import OnDeviceTrainingIcon from '@mui/icons-material/OnDeviceTraining';
import AccessTimeIcon from '@mui/icons-material/AccessTime';

const Superior=()=>{
  return(
    <div className='container-fluid'>
      <div className="row">
        <div className="col-sm-4">
          <img src={logo} width={190} height={100}/>
        </div>
        <div className="col-sm-4 mt-3">
          <div className="container">
            <div className="row">
              <div className="col-sm-2">
                <OnDeviceTrainingIcon color="primary" className='mt-2' sx={{ fontSize: 60 }}/>
              </div>
              <div className="col-sm-8">
                <h6 className="text-body-primary"> (+57) 301 793 4573</h6>
                <h6 className="text-body-primary"> (+57) 304 581 1986</h6>
                <h6 className="text-body-primary"> (+57) 300 479 4151</h6>
              </div>
            </div>
          </div>
        </div>

        <div className="col-sm-4">
          <div className="container mt-3">
            <div className="row">
              <div className="col-sm-3">
                <AccessTimeIcon color="primary" sx={{ fontSize: 60 }}/>
              </div>
              <div className="col-sm-8">
                <small className="text-body-secondary mt-2"> Lunes a viernes: de 8:00 AM - 6:00 PM</small>
                <small className="text-body-secondary"> Sábados: 8:00 Am - 12:00 M</small>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Superior