import * as React from 'react';
import Stack from '@mui/material/Stack';
import candelaria from '../assets/clientes/candelaria.jpeg'
import cartagena from '../assets/clientes/cartagena.png';
import hidroituango from '../assets/clientes/hidroituango.jpeg';
import sofia from '../assets/clientes/sofia.jpeg';
import zonafranca from '../assets/clientes/zonafranca.jpeg';

import Paper from '@mui/material/Paper';
import { styled } from '@mui/material/styles';

const Clientes=()=> {

  const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    ...theme.applyStyles('dark', {
      backgroundColor: '#1A2027',
    }),
  }));


  return (
    <div className='mt-3 mb-3 text-center' style={{ padding:20}}>
      <h1 className='text-muted'>NUESTROS CLIENTES</h1>
      <Stack
        direction={{ xs: 'column', sm: 'row' }}
        spacing={{ xs: 1, sm: 2, md: 4 }}
      >
        <Item> <img src={candelaria} width={190} height={100}/></Item>
        <Item><img src={cartagena} width={190} height={100}/></Item>
        <Item><img src={hidroituango} width={190} height={100} style={{ borderRadius:100}}/></Item>
        <Item><img src={sofia} width={190} height={100} style={{ borderRadius:100}}/></Item>
        <Item><img src={zonafranca} width={190} height={100}/></Item>
      </Stack>
    </div>
  );
}

export default Clientes