import * as React from 'react';
import Stack from '@mui/material/Stack';
import candelaria from '../assets/galeria/img1.jpg'
import cartagena from '../assets/galeria/img2.jpg'
import hidroituango from '../assets/galeria/img3.jpg'
import sofia from '../assets/galeria/im4.jpg'
import zonafranca from '../assets/galeria/img5.jpg'
import Paper from '@mui/material/Paper';
import { styled } from '@mui/material/styles';


const Galeria=()=> {
  const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    ...theme.applyStyles('dark', {
      backgroundColor: '#1A2027',
    }),
  }));

  return (
    <div className='mt-3 mb-3' style={{ padding:20}}>
      <h1 className='text-center'>GALERIA DE IMAGENES</h1>
      <Stack
        direction={{ xs: 'column', sm: 'row' }}
        spacing={{ xs: 1, sm: 2, md: 4 }}
      >
        <Item> <img src={candelaria} width={190} height={100}/></Item>
        <Item><img src={cartagena} width={190} height={100}/></Item>
        <Item><img src={hidroituango} width={190} height={100} style={{ borderRadius:100}}/></Item>
        <Item><img src={sofia} width={190} height={100} style={{ borderRadius:100}}/></Item>
        <Item><img src={zonafranca} width={190} height={100}/></Item>
      </Stack>
    </div>
  );
}

export default Galeria